<template>
	<div id="project-page">
		<div class="project container">
			<h3 class="text-center">My Project</h3>
			<p class="text-center font-courier" v-html="desc_animated">
			</p>
			<!-- FILTER -->
			<div class="row mt-5">
				<div class="col-lg-3 col-md-2"></div>
				<div class="col-lg-6 col-md-8">
					<div class="row my-1">
						<div class="col-sm-6 col-4 filter-label">
							<small>Search</small>
						</div>
						<div class="col-sm-6 col-8">
							<input type="text" class="form-control form-control-sm jon-form-dark" v-model="search">
						</div>
					</div>
					<div class="row my-1 filter-label">
						<div class="col-sm-6 col-4">
							<small>Sort By</small>
						</div>
						<div class="col-sm-6 col-8">
							<select class="form-control form-control-sm jon-form-dark" v-model="sort">
								<option value="id">Default</option>
								<option value="stargazers_count">Stars count</option>
								<option value="forks_count">Forks count</option>
								<option value="created_at">Date</option>
							</select>
						</div>
					</div>
				</div>
				<div class="col-lg-3 col-md-2"></div>
			</div>
			<!-- PROJECT -->
			<div v-if="!is_loading && project.length > 0" class="row mt-5">
				<div v-for="(pr) in project" :key="pr.id" class="col-md-4 col-sm-6 project-wrapper">
					<router-link :to="{ name: 'ProjectDetail', params: { slug: pr.slug } }" class="project-item">
						<div class="project-img-wrapper">
							<div
								class="project-img"
								:style="{ backgroundImage: 'url('+require('../assets/img/project/'+pr.thumb)+')' }">	
							</div>
						</div>
						
						<div class="project-info">
							<p>
								{{pr.title}}
								<span class="badge" :class="{'badge-primary': pr.github_name, 'badge-danger': !pr.github_name}">
									{{(pr.github_name) ? 'Public' : 'Private'}}
								</span>
							</p>
							<p v-if="pr.github">
								<small v-if="pr.github.stargazers_count || pr.github.forks_count">
									<i class="far fa-star"></i> {{pr.github.stargazers_count}}
									<svg aria-label="forks" class="svg-icon" viewBox="0 0 16 16" version="1.1" width="16" height="16" role="img"><path fill-rule="evenodd" d="M5 3.25a.75.75 0 11-1.5 0 .75.75 0 011.5 0zm0 2.122a2.25 2.25 0 10-1.5 0v.878A2.25 2.25 0 005.75 8.5h1.5v2.128a2.251 2.251 0 101.5 0V8.5h1.5a2.25 2.25 0 002.25-2.25v-.878a2.25 2.25 0 10-1.5 0v.878a.75.75 0 01-.75.75h-4.5A.75.75 0 015 6.25v-.878zm3.75 7.378a.75.75 0 11-1.5 0 .75.75 0 011.5 0zm3-8.75a.75.75 0 100-1.5.75.75 0 000 1.5z"></path></svg>
									{{pr.github.forks_count}}
								</small>
							</p>
							<small v-if="pr.github">{{ pr.github.description }}</small>
							<small v-else>{{pr.summary}}</small>
						</div>
					</router-link>
				</div>
			</div>
			<!-- LOADING -->
			<div v-if="is_loading" class="row mt-5">
				<div v-for="i in 6" class="col-md-4 col-sm-6 project-wrapper project-loader" :key="i">
					<a class="project-item">
						<div class="project-img-wrapper">
							<div class="project-img loader" style="background: #ccc"></div>
						</div>
						
						<div class="project-info">
							<div style="background: #ccc; padding:10px; width: 50%"></div>
							<div style="display: flex; margin-top: 5px">
								<div style="background: #ccc; padding:7px; width: 10px;margin-right: 5px"></div>
								<div style="background: #ccc; padding:7px; width: 10px;margin-right: 5px"></div>
								<div style="background: #ccc; padding:7px; width: 10px;margin-right: 5px"></div>
								<div style="background: #ccc; padding:7px; width: 10px"></div>
							</div>
							<div style="background: #ccc; padding:7px; width: 100%;margin-top: 5px"></div>
							<div style="background: #ccc; padding:7px; width: 5%;margin-top:5px"></div>
						</div>
					</a>
				</div>
			</div>

			<div v-if="project.length == 0" class="mt-5">
				<p class="text-center">Tidak ada project</p>
			</div>

			<div class="mt-5">
				<p class="text-center">
					<button v-for="(p) in num_page" class="btn mx-1" :class="(p == active_page) ? 'btn-primary' : 'btn-secondary' " :key="p" @click="setPage(p)">{{p}}</button>
				</p>
			</div>
			<p class="text-center my-4">
				<small>Created By Putu Jhonarendra. Pageviews: {{pageview.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")}}</small>
			</p>
			<ButtonToTop />
		</div>
		<div class="project-comp-area">
			<div class="cincintitik comp15"></div>
			<div class="lingktrans comp16"></div>
			<div class="awan3 comp17"></div>
			<div class="awan4 comp18"></div>
		</div>
	</div>
</template>

<script>
import axios from 'axios'
import viewCounter from './../mixins/viewCounter'
import projectRunningText from './../mixins/projectRunningText'
import '../assets/css/project.css'
import prj_data from '../data/project.json'
import git_data from '../data/repos.json'
import ButtonToTop from '../components/ButtonToTop'
export default {
	data(){
		return {
			project: [],
			project_all: [],
			// repos: git_data,
			repos: [],
			is_online: false,
			is_loading: true,

			//pagination
			num_page: 1,
			active_page: 1,
			search: '',
			sort: ''
		}
	},
	components: {
		ButtonToTop
	},
	mixins: [viewCounter, projectRunningText],
	methods:{
		initializeProject(){
			this.is_online = navigator.onLine
			if(this.is_online){
				this.getGithubData()
			} else {
				this.repos = git_data
				prj_data.forEach((e) => {
					if(e.github_name){
						e.github = this.getGithubByName(e.github_name)
					}
				})
				this.project = prj_data.slice(0, 6)
				this.project_all = prj_data
				this.setPagination()
			}
		},
		setPagination(){
			let per_page = 6
			let num_item = this.project_all.length
			let num_page = parseInt(num_item/per_page) + 1

			this.num_page = num_page
		},
		goBack(){
			window.history.back()
		},
		scrollTop(){
			window.scroll({
				top: 0,
				behavior: 'smooth'
			})
		},
		getGithubData(){
			axios
			.get('https://api.github.com/users/jhonarendra/repos')
			.then(res => {
				this.repos = JSON.parse(JSON.stringify(res.data))

				prj_data.forEach((e) => {
					if(e.github_name){
						// add new attribute 'github' for github data
						e.github = this.getGithubByName(e.github_name)
					}
				})
				this.project = prj_data.slice(0, 6)
				this.project_all = prj_data
				this.setPagination()
			})
		},
		getGithubByName(name){
			var github
			this.repos.forEach(function(e){
				if(e.name == name){
					github = e
				}
			})
			return github
		},
		handleScroll(){
			var scrollpx = window.scrollY
			var window_height = window.innerHeight
			var elements = document.getElementsByClassName('jo-show')
			for(var i = 0; i < elements.length; i++){
				var offset = scrollpx + elements[i].getBoundingClientRect().top
				var add_height = 100
				offset = offset - window_height + add_height
				if(scrollpx > offset){
					elements[i].classList.remove('hide-show')
					if('fade-show' in elements[i].classList == false){
						elements[i].classList.add('fade-show')
					}
				} else {
					elements[i].classList.remove('fade-show')
					elements[i].classList.add('hide-show')
				}
			}
		},
		setPage(val){
			this.active_page = val
			let per_page = 6
			// set range index yg diambil
			let max = val * per_page
			let min =  max - per_page

			this.project = this.project_all.slice(min, max)
		},
		searchProject(){
			let filtered = []
			this.project_all.forEach((e)=>{
				if(e.title.toLowerCase().indexOf(this.search.toLowerCase()) > -1){
					filtered.push(e)
				}
			})
			this.project = filtered.slice(0, 6)
		},
		sortProject(){
			this.is_loading = true
			let project_all = this.project_all
			const compare = ({key}) => {
				return (a, b) => {
					let result = 0
					if(a.github_name != '' && b.github_name != ''){
						// console.log(a)
						if(a.github[key] > b.github[key]){
							result = 1
						}

						if(a.github[key] < b.github.[key]){
							result = -1
						}
					} else {
						result = -1
					}



					if(key == 'id'){
						if(a[key] > b[key]){
							result = 1
						}

						if(a[key] < b.[key]){
							result = -1
						}

						return result
					}
					

					// kalo order desc tinggal dibalik (pake -1)
					return result * -1
				}
			}
			project_all = project_all.sort(compare({key: this.sort}))
			this.project = project_all.slice(0, 6)
			this.project_all = project_all


			setTimeout(()=>{
				this.is_loading = false
			}, 500)
		}
	},
	mounted(){
		this.getPageview() // call mixins
		this.initializeProject()
		this.descAnimated()
		// scroll func
		this.handleScroll()
		window.addEventListener('scroll', this.handleScroll)
		// hide element on mounted
		var elements = document.getElementsByClassName('jo-show')
		for(var i = 0; i < elements.length; i++){
			elements[i].classList.add('hide-show')
		}

		setTimeout(()=>{
			this.is_loading = false
		}, 2000)


		window.scroll({
			top: 0,
			behavior: 'smooth'
		})
	},
	watch:{
		search(){
			this.searchProject()
		},
		sort(){
			this.sortProject()
		}
	}
}
</script>